.double-text {
  position: relative;
}
.double-text:before {
  content: attr(data-name);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translate(-2%, 9%);
  color: #fff;
  text-shadow: 0 1px 0 #000, 0 -1px 0 #000, 1px 0 0 #000, -1px 0 0 #000;
  z-index: -1;
}
@supports (-webkit-text-stroke: 1px #000) {
  .double-text:before {
    color: transparent;
    -webkit-text-stroke: 1px #000;
    text-shadow: none;
  }
}
