.i-popup * {
  box-sizing: border-box;
}
.i-popup {
  position: fixed;
  z-index: -3000;
  opacity: 0;
  transition: opacity 0.5s, z-index 0s 0.5s;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.i-popup.opened {
  transition: opacity 0.5s, z-index 0s 0s;
  z-index: 3000;
  opacity: 1;
}
.i-popup__container {
  display: inline-block;
  position: relative;
  margin: 1rem 0;
  width: 100%;
  z-index: 2000;
  background: #fff;
  max-width: 1080px;
  width: 100%;
  vertical-align: middle;
  margin: 2%;
  transform: scale(0.5);
  opacity: 0;
  transition: 1s 0.5s;
}
.i-popup.opened .i-popup__container {
  transform: scale(1);
  opacity: 1;
}
.i-popup__box {
  display: flex;
}
.i-popup__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.75);
  z-index: 1000;
  cursor: url("data:image/png;base64,"), pointer;
}
.i-popup__box-content,
.i-popup__box-picture {
  width: 50%;
  position: relative;
  overflow: hidden;
}
.i-popup__box-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 80%;
  max-height: 80%;
}
.i-popup__box-content {
  padding: 80px;
  height: 600px;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.i-popup__box-content-inner {
  width: 100%;
  overflow: hidden;
}
.i-popup__box-title {
  text-align: center;
  margin-bottom: 30px;
  font-size: 16px;
}
.i-popup__box-title b {
  font-size: 28px;
}
.i-popup__check-title {
  text-align: center;
  margin-bottom: 40px;
  font-weight: bold;
}
.i-popup__check-text {
  text-align: center;
  margin-bottom: 20px;
}
.i-popup__form {
  display: block;
}
.i-popup__form input {
  display: block;
  margin-bottom: 20px;
  width: 100%;
}
.i-popup__input {
  height: 60px;
  padding: 0.25rem 1rem;
  font-size: 18px;
  border: 1px solid #b8b8b8;
  transition: 0.3s;
}
.i-popup__input.warn {
  animation: warn 0.5s infinite ease-in-out;
}
.i-popup__submit {
  border: none;
  height: 60px;
  margin-top: 40px;
  background: #1b742a;
  transition: 0.3s;
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  color: #fff;
  user-select: none;
  cursor: pointer;
}
.i-popup__submit:hover {
  background: #50b948;
}
.i-popup__button {
  display: block;
  text-align: center;
  background: none;
  border: none;
  font-size: 12px;
  color: #b8b8b8;
  margin: 20px auto;
  transition: 0.3s;
  user-select: none;
  cursor: pointer;
}
@media screen and (max-width: 1080px) {
  .i-popup__box-content {
    padding: 20px;
  }
}
@media screen and (max-width: 820px) {
  .i-popup__box-picture {
    display: none;
  }
  .i-popup__box-content {
    width: 100%;
  }
}
.preloader {
  min-height: 8rem;
  display: none;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background: rgba(255,255,255,0.9);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 100;
}
.preloader--active {
  display: flex;
}
.preloader svg {
  animation: preloader 30s infinite linear;
}
.preloader .load {
  opacity: 0;
  animation: loader 4.7s infinite linear;
}
.preloader .load-1 {
  animation-delay: 0s;
}
.preloader .load-2 {
  animation-delay: 0.5s;
}
.preloader .load-3 {
  animation-delay: 1s;
}
.preloader .load-4 {
  animation-delay: 1.5s;
}
.preloader .load-5 {
  animation-delay: 2s;
}
.preloader .load-6 {
  animation-delay: 2.5s;
}
.preloader .load-7 {
  animation-delay: 3s;
}
.preloader .load-8 {
  animation-delay: 3.5s;
}
@-moz-keyframes preloader {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes preloader {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-o-keyframes preloader {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-moz-keyframes loader {
  0%, 100% {
    opacity: 1;
  }
  60%, 80% {
    opacity: 0;
  }
}
@-webkit-keyframes loader {
  0%, 100% {
    opacity: 1;
  }
  60%, 80% {
    opacity: 0;
  }
}
@-o-keyframes loader {
  0%, 100% {
    opacity: 1;
  }
  60%, 80% {
    opacity: 0;
  }
}
@keyframes loader {
  0%, 100% {
    opacity: 1;
  }
  60%, 80% {
    opacity: 0;
  }
}
@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    color: #000 !important; /* Black prints faster: http://www.sanbeiji.com/archives/953 */
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  a[href^="#"]:after,
  a[href^="javascript:"]:after {
    content: "";
  }
  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  tr,
  img {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  h2,
  h3 {
    page-break-after: avoid;
  }
}
